* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-family: sans-serif;
  color: #eeeeee;
  background-color: #222831;
}

h2 {
  font-weight: 500;
}

.title {
  grid-column: 1/-1;
}

.container {
  max-width: 80rem;
  margin: 0 auto;
  padding: 2rem;
}

.grid {
  display: grid;

  gap: 2rem;
}

.grid-2-cols {
  grid-template-columns: 1fr 1fr;
}

.flex {
  display: flex;
  gap: 1.2rem;
  align-items: center;
}

input {
  padding: 0.8rem;
  border-radius: 7px;
  width: 50%;
  background-color: #eeeeee;
  color: #00686d;
}

.btn-submit {
  padding: 0.8rem 2.4rem;
  border-radius: 7px;
  cursor: pointer;
  background-color: #eeeeee;
  color: #00686d;
  font-weight: 600;
  font-size: 2rem;
}

.total-progress {
  font-size: 2rem;
}

form {
  grid-column: 1/-1;
}
